import React, { Component } from 'react';
import { pdfjs } from 'react-pdf';
import { Document, Page } from 'react-pdf';
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;
 
export default class PdfCv extends React.Component {
  state = {
    numPages: 3,
    pageNumber: 1,
  }
 
  onDocumentLoadSuccess = ({ numPages }) => {
    this.setState({ numPages });    
  }
 
  render() {
    const { pageNumber, numPages } = this.state;
 
    return (
      <div>
        {/* <Document
          file="/assets/pdf/CV_compressed.pdf"
          onLoadSuccess={this.onDocumentLoadSuccess}
        >
          <Page pageNumber={1} />
          <Page pageNumber={2} />
          <Page pageNumber={3} />
        </Document> */}
        {/* <p>Page {pageNumber} of {numPages}</p> */}
        <img style={{width:'100%'}} src="assets/svg/cv1.svg" alt="cv"/>
        <img style={{width:'100%'}} src="assets/svg/cv2.svg" alt="cv"/>
        <img style={{width:'100%'}} src="assets/svg/cv3.svg" alt="cv"/>
      </div>
    );
  }
}